<template>
  <div class="login">
    <div class="wrapper">
      <h3>Login</h3>
      <div class="logo-login"></div>

      <div v-show="hasUpdate">
        <font-awesome-icon icon="user" />
        <mt-field class="user-id" placeholder="User Id" type="text" v-model="userId"></mt-field>
        <font-awesome-icon icon="key" />
        <mt-field placeholder="Password" type="password" v-model="password"></mt-field>
        <mt-button type="primary" size="large" @click="onLogin">Login({{version}})</mt-button>
      </div>

      <div>
        <mt-button v-show="!hasUpdate" type="primary" size="large" @click="onUpdate">{{updateTitle}}</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import Utils from "../utils";
import BizRCHub from "../api/BizRCHub.js";

export default {
  name: "Login",
  data() {
    return {
      userId: "",
      password: "",
      isAndroid: false,
      hasUpdate: false,
      updateTitle: "Checking new version ...",
      updateBaseUrl: "https://rciupdatetest.azurewebsites.net/RCAppsHome",
      appName: "RCHub",
      version: "2.2.0"
    };
  },
  methods: {
    login(userId, password, ticks) {
      let uuid = "";
      let passwordHash = password;
      if (ticks.length == 0) {
        ticks = Utils.getTicks().toString();
        passwordHash = Utils.encryptPassword(ticks, password);
      }

      this.$showLoading();
      BizRCHub.Login(userId, passwordHash, ticks, uuid)
        .then(res => {
          if (res.data.OK) {
            this.$closeLoading();

            Utils.setRemeberMe(userId, passwordHash, ticks);
            let jobNumber = Utils.getJobNumber();

            let user = res.data.Result;
            let settings = {
              UsrAccountPK: user.UsrAccountPK,
              FirstName: user.FirstName,
              LastName: user.LastName,
              CurrentDateTime: user.CurrentDateTime,
              MinDateTime: user.MinDateTime,
              MaxDateTime: user.MaxDateTime,
              IsCompanyManager: user.IsCompanyManager,
              IsDivisionManager: user.IsDivisionManager,
              IsRegionManager: user.IsRegionManager,
              IsJobManager: user.IsJobManager,
              SearchJobNumber: jobNumber,
              SearchDateTime: user.CurrentDateTime,
              SearchRegionPK: "",
              SearchRegionName: ""
            };

            Utils.setUserToken(userId, passwordHash, ticks, "");

            if (!jobNumber) {
              this.$prompt("Please input job number")
                .then(({ value, action }) => {
                  console.log(action);
                  Utils.setJobNumber(value);
                  settings.SearchJobNumber = value;
                  Utils.setUserSettings(settings);
                  this.$router.push({ name: "JobTimesheetList" });
                })
                .catch(err => {
                  console.log(err);
                  Utils.setUserSettings(settings);
                  this.$router.push({ name: "JobTimesheetList" });
                });
            } else {
              Utils.setUserSettings(settings);
              this.$router.push({ name: "JobTimesheetList" });
            }
          } else {
            BizRCHub.LoginByEmployee("", "", "", userId, password, uuid)
              .then(res => {
                this.$closeLoading();
                if (res.data.OK) {
                  Utils.setRemeberMe(userId, password, ticks);

                  let user = res.data.Result;
                  let settings = {
                    IsEmployee: true,
                    HREmployeePK: user.HREmployeePK,
                    FirstName: user.FirstName,
                    LastName: user.LastName,
                    CurrentDateTime: user.CurrentDateTime,
                    MinDateTime: user.MinDateTime,
                    MaxDateTime: user.MaxDateTime
                  };

                  Utils.setUserToken(
                    user.UserId,
                    user.Password,
                    user.Ticks,
                    user.Secret
                  );
                  Utils.setUserSettings(settings);
                  this.$router.push({ name: "EmployeeTimesheetList" });
                } else {
                  this.$message({ message: res.data.Error, type: "error" });
                  console.log(res.data.Error);
                }
              })
              .catch(err => {
                this.$closeLoading();
                this.showNetworkError();
                console.log(err);
              });
          }
        })
        .catch(err => {
          this.$closeLoading();
          this.showNetworkError();
          console.log(err);
        });
    },
    checkUserInput() {
      if (!this.userId) {
        this.$alert("The user id can not be empty.", "Warning");
        return false;
      }

      if (!this.password) {
        this.$alert("The password can not be empty.", "Warning");
        return false;
      }

      return true;
    },
    onLogin() {
      if (!this.checkUserInput()) return;

      this.login(this.userId, this.password, "");
    },
    checkAndroidVersion() {
      let url = `${this.updateBaseUrl}/${this.appName}/${this.appName}.txt`;
      axios
        .get(url)
        .then(res => {
          let latestVersion = res.data.Version;
          if (latestVersion != this.version) {
            this.updateTitle = `Update V${this.version} to V${latestVersion}`;
            this.onUpdate();
          } else {
            this.hasUpdate = true;

            let user = Utils.getRemeberMe();
            if (user.UserId.length > 0) {
              this.userId = user.userId;
              this.login(user.UserId, user.Password, user.Ticks);
            }
          }
        })
        .catch(err => {
          console.log(err);
          this.$alert(err);
        });
    },
    checkIOSVersion() {
      let url = `${this.updateBaseUrl}/${this.appName}/${this.appName}.plist`;
      axios
        .get(url)
        .then(res => {
          let latestVersion = "";
          const splits = res.data.split(/\r\n|[\r\n]/);
          for (let index = 0; index < splits.length; index++) {
            const line = splits[index];
            if (line.endsWith("<key>bundle-version</key>")) {
              if (index + 1 < splits.length) {
                const i = splits[index + 1].indexOf("<string>");
                latestVersion = splits[index + 1].substring(i + 8, i + 8 + 5);
              }
            }
          }

          if (latestVersion.length > 0 && latestVersion != this.version) {
            this.updateTitle = `Update V${this.version} to V${latestVersion}`;
            this.onUpdate();
          } else {
            this.hasUpdate = true;

            let user = Utils.getRemeberMe();
            if (user.UserId.length > 0) {
              this.userId = user.userId;
              this.login(user.UserId, user.Password, user.Ticks);
            }
          }
        })
        .catch(err => {
          console.log(err);
          this.$alert(err);
        });
    },
    onUpdate() {
      let url = "";
      if (this.isAndroid) {
        url = `${this.updateBaseUrl}/${this.appName}-update-apk.html`;
      } else {
        url = `${this.updateBaseUrl}/${this.appName}-update-ipa.html`;
      }
      window.open(url, "_blank");
    }
  },
  mounted() {
    var u = navigator.userAgent;
    this.isAndroid =
      u.indexOf("Android") > -1 ||
      u.indexOf("Linux") > -1 ||
      u.indexOf("Chrome") > -1;

    if (this.isAndroid) {
      this.checkAndroidVersion();
    } else {
      this.checkIOSVersion();
    }
  }
};
</script>

<style lang="scss" scoped>
.login {
  height: 100%;
  background: #c45050;
  background: linear-gradient(to bottom, #7a1811, #f06260);
}
.mint-cell {
  background-color: transparent;
  border-bottom: 1px solid #d0d0d0;
}
.mint-cell-title {
  display: none;
}
.mint-button {
  margin-top: 30px;
  background-color: #f06260;
  border-radius: 20px;
  cursor: pointer;
}
.mint-button:hover {
  background-color: #f04f4d;
}
h3 {
  display: none;
}
.logo-login {
  height: 200px;
  background: url(../assets/logo_login.png) center bottom no-repeat;
}
.wrapper {
  width: 80%;
  margin: 0 auto;
}
svg {
  float: left;
  position: relative;
  top: 32px;
  left: 12px;
  color: #fff;
}
.fa-user {
  top: 44px;
}
.wrapper .mint-cell {
  width: 100%;
}
.wrapper .mint-cell.user-id {
  top: 12px;
}
</style>
<style lang="scss">
.login .mint-field-core {
  background-color: transparent;
  padding-left: 35px;
  color: #fff;
}
.login input::-webkit-input-placeholder {
  color: #d0d0d0;
}
.login input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #d0d0d0;
}
.login input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #d0d0d0;
}
.login input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #d0d0d0;
}
.mint-msgbox-confirm {
  color: #a9362d;
}
.update-class {
  font-size: 18px;
  text-decoration: none;
  color: white;
  padding-top: 10px;
}
</style>
